import { Component } from '@angular/core';
import {MetaDataService} from "./services/meta-data.service";

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  public viewProduct:any;
  public toggleAddProduct:boolean = false;
  private selectedValue:string = 'name';
  private  details:any = ['description', 'name', 'price'];

  constructor(private metaDataService:MetaDataService) {
    this.metaDataService.getData().subscribe(data=> {
      this.metaDataService.products = data;
    })
  }

  selectedProduct(data) {
    this.viewProduct = Object.assign({}, data);
  }

  closePopupAddProduct(toggle) {
    this.toggleAddProduct = toggle
  }
}
